import React, { Component } from 'react';
// import logo from './logo.svg';
import './down1.css';
import box2 from '../../images/yushou_03.png'
import box1 from '../../images/join_01.jpg'
import img from '../../images/join_01.jpg'
import {HashRouter as Router,Route,Link} from 'react-router-dom'
class Down1 extends Component {
  state={
    app1:[
      {image:img},{image:img}
    ]
  }
  render (){
    const list = this.state.app1.map((t, i) => (
      <div key={i} className="listd clearfix">
        <div className="pic clearfix">
          <a href="#">
            <img className="box1" src={box1} alt=""/>
          </a>
          <span>
            <img className="box2" src={box2} alt=""/>
            </span>
          <a href="#" className="words">
          Boss T
          </a>
        </div>
        <div className="size">
          <span>iphone 5</span>
          <span>XL</span>
          <span><em>79</em></span>
          <span>x1</span>
        </div>
      </div>
    ))
    return(
      <div className='down1'>

        <div className="adr goods clearfix">
          <p></p>
          <a href="#"></a>
        </div>
           {list}

           <div className="messege clearfix">
              <p className="clearfix">
                <span></span>
                <a href="#"></a>
              </p>
              <p className="clearfix">
                <span></span>
                <a href="#"></a>
              </p>
              <p className="invoice clearfix">
                <span></span>
                <a href="#" className="no"></a>
                <a href="#" className="yes active3"></a>
              </p>
              <p class="yesinvoice  clearfix">

                <input type="text" value="/"/>
                <a href="#" className='save'></a>
              </p>
            </div>
            <div className="other clearfix">
              <p></p>
              <input type="text" className="text"/>
              <p>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </p>
              <p>
                <span>2</span>
                <span>x</span>
                <span>79.00</span>
                <span>=</span>
                <span>158.00</span>
                <span>-</span>
                <span>0.00</span>
                <span>+</span>
                <span>10.00</span>
                <span>=</span>
                <span>168.00</span>
              </p>
              <div className="clearfix">
                <Link to='/Ding'><a href="counter.html" className="btn"></a></Link>
              </div>
            </div>

      </div>
    )
  }
}
export default Down1



