import React, { Component } from 'react';
// import logo from './logo.svg';
import './down.css';
import {HashRouter as Router,Route,Link} from 'react-router-dom'

class Down extends Component {
  render (){
    return(
      <div className='down'>
          <div className="end">
            <div className="endin clearfix">
              <div className="endleft clearfix">
                <input type="checkbox" id="all1" className='all' />
                <label for="all1"></label>
                <a href="#" className="deldown"><i class="iconfont icon-trash"></i></a>

                <p> <span className="totalqty"></span> , <span className="selectnum"></span> </p>
                <div class="clearfix">
                  <p class="clearfix">
                    <span>:</span>
                    <span>[]</span>
                  </p>
                  <p><span className="total">0</span></p>
                </div>
              </div>
              <div className="endright">
                <Link to='/List'><a href="order.html" className="gosub"></a></Link>
              </div>
            </div>
          </div>
      </div>
    )
  }
}
export default Down



