import React, { Component } from 'react';
// import logo from './logo.svg';
import './list.css';
import Listdown from'../Listdown/Down1'
import img from '../../images/join_01.jpg'
class List extends Component {
  state={
    app1:[
      {image:img},{image:img},{image:img}
    ]
  }
  render (){
    const list = this.state.app1.map((t, i) => (
      <div key={i}className="list clearfix">
        <ul>
          <li>
            <i className="iconfont icon-weibiaoti1"></i>
          </li>
          <li>
            <i className="iconfont icon-weibiaoti3"></i>
          909513</li>
          <li>
            <i className="iconfont icon-phone"></i>186***5194
          </li>
        </ul>
        <div className="clearfix">
          <a href="#" className="set"></a>
          <a href="#" className="editor"><i class="iconfont icon-bianji"></i></a>
          <a href="#" className="del"><i class="iconfont icon-trash"></i></a>
        </div>
      </div>
    ))
    return(
      <div className='lists'>
            <div className="joinup">
              <div className=" shop9 ">
                <p>
                    <div className="word clearfix">
                      <span></span>
                      <span></span>
                      <span></span>
                    </div>
                </p>
              </div>
            </div>
            <div className="joindown">
                <div className="adr clearfix">
                  <p></p>
                  <a href="#" className="setnew"></a>
                </div>
                  {list}
                  <div className="showmore">
    								<a href="#" className="more"></a>
    								<a href="#" className="toup"></a>
    							</div>
            </div>
            <Listdown/>
      </div>
    )
  }
}
export default List



